import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";

import { Search } from "react-iconly";
import { connect } from "react-redux";

import MasterPageTitle from "../../../../layout/components/content/master-page-title";

import {
  getModels,
  modelsEdit,
  modelDelete,
  isModelVisible,
  SetModelsParams,
} from "../../../../store/Masters/Models/modelsSlice";

import { getMiniBrandFamily } from "../../../../store/mini/miniSlice";

import AddModels from "./addModels";

function Models(props) {
  useEffect(() => {
    props.getModels({ page: 1, search: "", page_size: 10 });
  }, []);
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";
  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "20%",
    },
    {
      title: "Model Name",
      dataIndex: "name",

      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "20%",
    },
    {
      title: "Brand Name",
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "20%",
      render: (row) => <span className={columnClass}>{row.brand?.name}</span>,
    },
    {
      title: "Brand Family Name",
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "20%",
      render: (row) => (
        <span className={columnClass}>{row.brandfamily?.name}</span>
      ),
    },
    {
      title: "Colors",
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "20%",
      render: (row) => (
        <span className={columnClass}>
          {row.color.map((c) => c?.name).join(", ")}
        </span>
      ),
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_model") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.modelsEdit(row);
              props.getMiniBrandFamily({ brand: row.brand.id });
              props.isModelVisible(true);
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Masters.delete_model") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.modelDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
)}
        </Space>
      ),
    },
  ];

  const data = props.modelslist.map((row, key) => ({
    ...row,
    index:
      (props.modelsParams.page - 1) * props.modelsParams.page_size + key + 1,
  }));

  function onPageChange(page, pageSize) {
    props.SetModelsParams({
      ...props.modelsParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getModels({
      ...props.modelsParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetModelsParams({ ...props.modelsParams, search: val });
    props.getModels({ ...props.modelsParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  const { Search } = Input;
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Models Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_model") !== -1 &&
          "Add Model"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={14} className="hp-mb-12">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getModels({
                      ...props.modelsParams,
                      page: 1,
                      search: "",
                    });
                    props.SetModelsParams({
                      ...props.modelsParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.modelsParams?.page}
                pageSize={props.modelsParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddModels />
    </Row>
  );
}

function mapStateToProps({ models, system }) {
  return {
    modelslist: models.modelslist,
    noofpages: models.noofpages,
    listCount: models.listCount,
    modelsParams: models.modelsParams,
    loading: models.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getModels,
  modelsEdit,
  modelDelete,
  isModelVisible,
  SetModelsParams,
  getMiniBrandFamily,
})(Models);
