import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col, Select } from "antd";
import {
  modelAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/Models/modelsSlice";
import { getMiniBrands, getMiniBrandFamily, getMiniColors } from "../../../../store/mini/miniSlice";

const AddModels = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {
    props.getMiniBrands(),
      props.getMiniColors();
  }, []);
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      name: props.rowdata?.name,
      brand_id: props.rowdata.brand_id,
      brandfamily_id: props.rowdata.brandfamily_id,
      color_ids: props.rowdata.color_ids,
    };
    if (obj.name != null || obj.name == "") {
      props.modelAdd(obj);
    }
  };


  // Updates Country in Store
  const handleUpdate = () => {
    const obj = {
      id: props.rowdata?.id,
      name: props.rowdata?.name,
      brand_id: props.rowdata.brand_id,
      brandfamily_id: props.rowdata.brandfamily_id,
      color_ids: props.rowdata.color_ids,
    };
    props.modelAdd(obj);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.rowdata.id == null || props.rowdata.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.rowdata.id == null || props.rowdata.id == 0 ? "Add" : "Update"}{" "}
      Models
    </h5>
  );

  const onSearch = (value) => {
    console.log('search:', value);
  };

  useEffect(() => {
    form.setFieldsValue({
      brand_id: props.rowdata.brand_id,
      brandfamily_id: props.rowdata.brandfamily_id,
      color_ids: props.rowdata.color_ids,
      name: props.rowdata.name,
    })
  }, [props.rowdata]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={416}
    >
      <Form layout="vertical"
        form={form}
        autoComplete="off"
        onFinish={() => {
          if (props.rowdata.id == null) {
            handleAdd();
          }
          else {
            handleUpdate();
          }
        }}>
        <Form.Item label="Brand Name:"
          name="brand_id"
          rules={[
            { required: true, message: "Please select your Brand Name!" },
          ]}
          hasFeedback>
          <Select
            showSearch
            placeholder="Select  Brand"
            optionFilterProp="children"
            onSearch={onSearch}
            filterOption={(input, option) => option.children.toLowerCase().includes(input.toLowerCase())}
            onChange={(value) => {
              props.getMiniBrandFamily({ brand: value });
              props.InputChangeValue({
                key: "brand_id",
                value: value,
              });
            }}
            value={props.rowdata.brand_id == null ? "" : props.rowdata?.brand_id}
          >
            {props.miniBrandsList.map((brand, index) => {
              return (
                <Option key={index} value={brand.id}>
                  {brand.name}
                </Option>
              );
            })}
          </Select>
        </Form.Item>

        <Form.Item label="Brand Family :"
          name="brandfamily_id"
          rules={[
            { required: true, message: "Please select your Brand Family!" },
          ]}
          hasFeedback>
          <Select
            showSearch
            placeholder="Select Brand Family"
            optionFilterProp="children"
            onSearch={onSearch}
            filterOption={(input, option) => option.children.toLowerCase().includes(input.toLowerCase())}
            onChange={(value) => {
              props.InputChangeValue({
                key: "brandfamily_id",
                value: value,
              });
            }}
            value={
              props.rowdata.brandfamily_id == null ? "" : props.rowdata?.brandfamily_id
            }
          >
            {props.miniBrandFamilyList.map((brandfamily, index) => {
              return (
                <Option key={index} value={brandfamily.id}>
                  {brandfamily.name}
                </Option>
              );
            })}
          </Select>
        </Form.Item>

        <Form.Item label="Color Name:"
          name="color_ids"
          rules={[
            { required: true, message: "Please input your Color Name!" },
          ]}
          hasFeedback>
          <Select
            mode="multiple"
            showSearch
            placeholder="Select Colors"
            optionFilterProp="color"
            maxTagCount="responsive"
            onChange={(value) => {
              props.InputChangeValue({
                key: "color_ids",
                value: value,
              });
            }}
            value={props.rowdata?.color_ids}
            options={props.miniColorsList.map((color, index) => {
              return { label: color.name, value: color.id };
            })}
          />

        </Form.Item>

        <Form.Item label="Model Name :"
          name="name"
          rules={[
            { required: true, message: "Please input your Model Name!" },
          ]}
          hasFeedback>
          <Input
            id="Zone"
            name="name"
            style={{ width: "100%" }}
            placeholder="Model Name"
            value={props.rowdata?.name}
            onChange={ChangeHandeler}
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ models, mini }) {
  return {
    rowdata: models.rowdata,
    model: models.model,
    miniBrandsList: mini.miniBrandsList,
    miniBrandFamilyList: mini.miniBrandFamilyList,
    miniColorsList: mini.miniColorsList,
  };
}

export default connect(mapStateToProps, {
  modelAdd,
  isModelVisible,
  InputChangeValue,
  getMiniBrands,
  getMiniBrandFamily,
  getMiniColors
})(AddModels);
